  --ice-coffee: #efd478;
  --milkshakes: #d6936d;
  --tintIcecream: rgba(156, 190, 143, .7);
  --tintIcecoffee: rgba(248, 210, 78, .7);
  --tintMilkshake: rgba(213, 117, 63, .7);
  --formBG: rgba(248, 210, 78, .1);
  --modalGreen: #9cbe8f;
  --BTN: #f8d24e;
  --accentBTN: #0a8817;
  --disabledBTN: #ababab;
  --Gap: 16px;
  --cubic: cubic-bezier(.4, 0, .2, 1);
